export default {
	props:["page"],
	data() {
		return {
			focus: "",
			friend: ""
		};
	},
	methods: {
		
	},
	mounted() {
		if(this.page === "friend") {
			// 这里不要赋值错了,要进行对应
			this.friend = "border-bottom: 2px solid #ffffff;";
		}else {
			this.focus = "border-bottom: 2px solid #ffffff;";
		}
	}
}
